<template>
    <div class="project-list">
        <div class="project" v-for="project in projects" :key="project.ID">
            <h3 class="title">{{project.APP_NAME}}</h3>
            <div class="project-imgs">
                <img v-for="(img,index) in project.imgs" :key="index" :src="img.img_url"/>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "project-list",
        data() {
            return{
                projects:[]
            }
        },
        methods: {
            getAppImgs(project){
                const query = this.Bmob.Query("T_IMG");
                query.equalTo("app_id","===", project.APP_ID);
                query.find().then(res => {
                    console.log(res);
                    this.$set(project,"imgs",res);
                });
            },
            getProjects() {
                const query = this.Bmob.Query("T_APP");
                query.order("ID");
                query.find().then(res => {
                    console.log(res);
                    this.projects = res;
                    this.projects.forEach(project => {
                        this.getAppImgs(project);
                    });
                });
            }
        },
        created() {
            this.getProjects();
        }
    }
</script>

<style lang="scss">
    .project-list {
        width: 80%;
        margin: 0 auto;
        .project{
            /*background-color: cadetblue;*/
            width: 100%;
            .title{
                padding-left: 20px;
            }
        }
        .project-imgs{
            height: 400px;
            overflow-x: scroll;
            white-space: nowrap;
            /*-webkit-overflow-scrolling:touch;*/
            img {
                user-select: none;
                width: 200px;
                height: auto;
                margin-left: 20px;
            }
        }
    }
</style>